//
// Colors
//

.has-text-primary { color: var(--color-primary) !important; }
.has-text-accent  { color: var(--color-accent) !important; }
.has-text-white   { color: var(--color-white) !important; }
.has-text-light   { color: var(--color-contrast-light) !important; }
.has-text-dark    { color: var(--color-contrast-high) !important; }
.has-text-darker  { color: var(--color-contrast-higher) !important; }
.has-text-black   { color: var(--color-black) !important; }
.has-text-current-color { color: currentColor !important; }
.has-text-inherit { color: inherit !important; }

.has-background-primary { background-color: var(--color-primary) !important; }
.has-background-accent  { background-color: var(--color-accent) !important; }
.has-background-white   { background-color: var(--color-white) !important; }
.has-background-light   { background-color: var(--color-contrast-light) !important; }
.has-background-dark    { background-color: var(--color-contrast-high) !important; }
.has-background-darker  { background-color: var(--color-contrast-higher) !important; }
.has-background-black   { background-color: var(--color-black) !important; }
.has-background-current-color { background-color: currentColor !important; }

//
// Common spacing values
//

.has-padding-top               { padding-top: var(--space-medium) !important; }
.has-padding-bottom            { padding-bottom: var(--space-medium) !important; }
.has-margin-top                { margin-top: var(--space-medium) !important; }
.has-margin-bottom             { margin-bottom: var(--space-medium) !important; }

$spacing-sizes: xxxsmall, xxsmall, xsmall, small, medium, large, xlarge, xxlarge, xxxlarge;
@each $size in $spacing-sizes {
  .has-padding-top-#{$size}    { padding-top: var(--space-#{$size}) !important; }
  .has-padding-bottom-#{$size} { padding-bottom: var(--space-#{$size}) !important; }
  .has-margin-top-#{$size}     { margin-top: var(--space-#{$size}) !important; }
  .has-margin-bottom-#{$size}  { margin-bottom: var(--space-#{$size}) !important; }
}

//
// Text sizes
//

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    @for $i from 1 through 6 {
      .is-size#{$infix}-#{$i} {
        font-size: var(--text-#{$i}) !important;
      }
    }
  }
}

//
// Typography
//

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    .has-text#{$infix}-left     { text-align: left !important; }
    .has-text#{$infix}-right    { text-align: right !important; }
    .has-text#{$infix}-centered { text-align: center !important; }
  }
}

.has-text-weight-light    { font-weight: 300 !important; }
.has-text-weight-normal   { font-weight: 400 !important; }
.has-text-weight-semibold { font-weight: 600 !important; }
.has-text-weight-bold     { font-weight: 700 !important; }

.is-lowercase {
  text-transform: lowercase !important;
}

.is-uppercase {
  text-transform: uppercase !important;
}

.is-italic {
  font-style: italic !important;
}

//
// Accessability
//

// Make elements visually invisible but still accessible to screen-readers
//
// "This Css has been carefully tested to ensure screen-readers can read and
// activate (in case of links and buttons) the elements with this class. Please
// use caution when changing anything, even seemingly safe ones. For example
// changing width from 1 to 0 would prevent TalkBack from activating (clicking)
// buttons despite TalkBack reading them just fine. This is because
// element needs to have a defined size and be on viewport otherwise TalkBack
// does not allow activation of buttons."
//
// Source: https://github.com/ampproject/amphtml/blob/master/css/amp.css
.is-sr-only {
  position: fixed !important;
  // Keep it in viewport
  top: 0px !important;
  left: 0px !important;
  // Give it non-zero size, VoiceOver on Safari requires at least 2 pixels
  // before allowing buttons to be activated
  width: 4px !important;
  height: 4px !important;
  // Visually hide it with overflow and opacity
  opacity: 0 !important;
  overflow: hidden !important;
  // Remove any margin or padding
  border: none !important;
  margin: 0 !important;
  padding: 0 !important;
  // Ensure no other style sets display to none
  display: block !important;
  visibility: visible !important;
}

//
// Miscellaneous
//

.is-marginless {
  margin: 0 !important;
}

.is-paddingless {
  padding: 0 !important;
}

.is-radiusless {
  border-radius: 0 !important;
}

.is-shadowless {
  box-shadow: none !important;
}

.is-unselectable {
  @extend %unselectable;
}

.is-contained {
  object-fit: contain !important;
}

.is-covered {
  object-fit: cover !important;
}

//
// Common `display` values
//

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    .is#{$infix}-hidden { display: none !important; }
    .is#{$infix}-inline { display: inline !important; }
    .is#{$infix}-block  { display: block !important; }
  }
}
